<template>
  <!-- 厂商详情 -->
  <div class="plan_box">
    <div class="contain">
      <div class="item_header">基本信息</div>
      <div class="item_two">
        <div class="code">
          <small>厂商编码</small>
          <div class="code_box">
            <el-input size="mini" v-model="code" placeholder=""></el-input>
          </div>
          <div class="icon">
            <i>*</i>
          </div>
        </div>
        <div class="code">
          <small>厂商名称</small>
          <div class="code_box">
            <el-input size="mini" v-model="name" placeholder=""></el-input>
          </div>
          <div class="icon">
            <i>*</i>
          </div>
        </div>
      </div>
      <div class="item_two">
        <div class="code">
          <small>联系人</small>
          <div class="code_box">
            <el-input size="mini" v-model="contacts" placeholder=""></el-input>
          </div>
        </div>
        <div class="code">
          <small>联系电话</small>
          <div class="code_box">
            <el-input size="mini" v-model="telephone" placeholder=""></el-input>
          </div>
        </div>
      </div>
      <div class="item_two line_box">
        <div class="code">
          <small>备注</small>
          <div class="code_box">
            <el-input
              size="mini"
              v-model="remarks"
              type="textarea"
              :rows="1"
            ></el-input>
          </div>
        </div>
      </div>
    </div>
    <div class="footer">
      <div class="footer_title">
        <p>注册证列表</p>
        <el-button
          type="primary"
          size="mini"
          @click="reset()"
          >添 加</el-button
        >
      </div>
      <div class="power_main">
        <el-table
          :data="tableData"
          :span-method="objectspanmethod"
          border
          style="width: 100%"
          size="mini"
          :height="$store.state.innerheight - 450"
          :header-cell-style="{ background: '#f5f5f5' }"
        >
          <el-table-column prop="registration_name" label="注册证名称" width="">
          </el-table-column>
          <el-table-column prop="registration_number" label="注册证号" width="">
          </el-table-column>
          <el-table-column
            prop="registration_validity_period"
            label="注册证有效期"
            width=""
          >
          </el-table-column>
          <el-table-column prop="remarks" label="备注" width="">
          </el-table-column>
          <el-table-column prop="registered_products" label="注册产品" width="">
          </el-table-column>
          <el-table-column
            prop="product_specification"
            label="产品规格"
            width=""
          >
          </el-table-column>
          <el-table-column prop="article_number" label="货号" width="">
          </el-table-column>
          <el-table-column prop="storage_conditions" label="储存条件" width="">
          </el-table-column>
          <el-table-column fixed="right" label="操作" width="">
            <template #default="scope">
              <el-button @click="handleEdit(scope.row)" type="text" size="mini"
                >编辑</el-button
              >
              <el-button
                style="color: red"
                type="text"
                size="mini"
                @click="handleDel(scope.row)"
                >移除</el-button
              >
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
  <div>
      <manufacturerDialog ref="manufacturerDialog"></manufacturerDialog>
  </div>
</template>

<script>
import { reactive, toRefs, onMounted } from "vue";
import { getCurrentInstance } from "@vue/runtime-core";
import manufacturerDialog from "../manufacturer_management/manufacturerDialog.vue"
export default {
    components: {
    manufacturerDialog,
  },
  setup() {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      code: "",
      name: "",
      contacts: "",
      telephone: "",
      remarks: "",
      tableData: [
        {
          code:1,
          registration_name: "证1",
          registration_number: "GBW(E)090073",
          registration_validity_period: "2022/6/25 0:00:00",
          remarks: "",
          registered_products: "心肌标记质控物",
          product_specification: "水平1: 6×3ml",
          article_number: "145",
          storage_conditions: "冷藏",
        },
        {
          code:1,
          registration_name: "证1",
          registration_number: "GBW(E)090073",
          registration_validity_period: "2022/6/25 0:00:00",
          remarks: "",
          registered_products: "心肌标记质控物",
          product_specification: "水平3: 3×3ml",
          article_number: "146",
          storage_conditions: "冷藏",
        },
        {
          code:2,
          registration_name: "证2",
          registration_number: "京械注准20162400101",
          registration_validity_period: "2022/6/25 0:00:00",
          remarks: "",
          registered_products: "C-肽质控物",
          product_specification: "水平3: 3×3ml",
          article_number: "147",
          storage_conditions: "冷藏",
        },
      ],
    });
    const reset = () => {
        proxy.$refs.manufacturerDialog.setshow();
    };

    const handleEdit = () => {
        proxy.$refs.manufacturerDialog.setedit();
    };
    const handleDel = () => {};
    // 合并表格的根据某个id值
    const objectspanmethod = ({row,column,rowIndex,columnIndex}) =>{
      console.log(row)
      console.log(column)
      for(var i = 0; i < 5; i++){
        if(i==columnIndex) {
          const _row = proxy.spanArr[rowIndex]
          const _col = _row > 0 ? 1 : 0
          return {
            rowspan:_row,
            colspan:_col
          }
        }
      }
      if(columnIndex==7){
        const _row = proxy.spanArr[rowIndex]
          const _col = _row > 0 ? 1 : 0
          return {
            rowspan:_row,
            colspan:_col
          }
      }
    }
    // 先把获取到的数据按规定值相同的放一起
    const rowspan = () => {
      proxy.spanArr = []
      proxy.position = 0
      proxy.tableData.forEach((item,index) => {
        if(0 == index) {
          proxy.spanArr.push(1)
          proxy.position = 0
        }else{
          if(proxy.tableData[index].code == proxy.tableData[index - 1].code){
            proxy.spanArr[proxy.position] += 1
            proxy.spanArr.push(0)
          }else{
            proxy.spanArr.push(1)
            proxy.position = index
          }
        }
      })
      console.log(proxy.spanArr)
    }
    onMounted(()=>{
      proxy.rowspan()
    })
    return {
      ...toRefs(state),
      handleEdit,
      handleDel,
      reset,
      objectspanmethod,
      rowspan,
    };
  },
};
</script>

<style  scoped>
.plan_box {
  /* display: flex; */
  width: 100%;
}

.contain {
  margin: 10px;
  border: 1px solid #e0e0e0;
}
.footer {
  margin: 10px;
  border: 1px solid #e0e0e0;
}
.power_main {
  padding: 0px 10px;
  box-sizing: border-box;
}
.item_header {
  font-size: 15px;
  font-weight: bold;
  padding: 10px;
  box-sizing: border-box;
}
.footer_title {
  padding: 10px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
}
.footer_title > p {
  font-size: 15px;
  font-weight: bold;
  margin: 0;
}

.item_two {
  display: flex;
  align-items: center;
  padding-top: 10px;
}

.code_box {
  width: calc(100% - 120px);
}
.code {
  width: 49%;
  display: flex;
  align-items: center;
}
.code > small {
  width: 80px;
  text-align: right;
  margin-right: 5px;
}

.line_box {
  margin-bottom: 10px;
}
.icon {
  color: red;
}
</style>
<style>
.el-tabs__nav-wrap {
  padding: 0 15px;
  box-sizing: border-box;
}
.eltabs.el-tabs .el-tabs__header {
  padding-top: 10px !important;
}
</style>