    const state = reactive({
      dialogVisible: false,
      registration_name: "",
      remarks: "",
      tableData1: [
        {
          registration_number: "GBW(E)090074",
          registration_validity_period: "2021/6/01 00:00:00",
          current_use: "当前使用",
        },
        {
          registration_number: "GBW(E)090074",
