          registered_products: "心肌标记质控物",
          product_specification: "水平1: 6×3ml",
          article_number: "145",
          storage_conditions: "冷藏",
        },
        {
          registered_products: "心肌标记质控物",
          product_specification: "水平1: 6×3ml",
          article_number: "145",
          storage_conditions: "冷藏",
        },
      ],
    });
    const { proxy } = getCurrentInstance();
    const setshow = () => {
      proxy.dialogVisible = true;
    };
    const setedit = () => {
      proxy.dialogVisible = true;
    };
    const reset = () => {};
    const handleEdit = () => {};
    const handleDel = () => {};
    const save = () => {
      proxy.dialogVisible = false;
    };

    const odd = () => {
      proxy.dialogVisible = false;
    };

    return {
      setshow,
      setedit,
      ...toRefs(state),
      handleEdit,
      handleDel,
      odd,
      save,
      reset,
    };
  },
};
</script>
